import axios from 'axios'
import 'we-vue/lib/style.css'
import { Dialog } from "we-vue"
import { Toast } from 'we-vue'

//
const randomString = function () {
    let len = 6,
        $chars = 'ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678',
        /****oO,Ll,9gq,Vv,Uu,I1****/
        maxPos = $chars.length,
        pwd = '';
    for (let i = 0; i < len; i++) {
        pwd += $chars.charAt(Math.floor(Math.random() * maxPos));
    }
    return pwd;
};

//
const weChatAuthRedirect = function (appId, authURL, redirectURL) {
    console.log('weChatAuthRedirect appId==' + appId + ' authURL==' + authURL + ' redirectURL==' + redirectURL);

    // 
    let tempRedirectURL = encodeURIComponent(authURL),
        //  URL, openIdURL, 
        //let tempIndexURL = encodeURIComponent(redirectURL);
        //alert('tempIndexURL=='+decodeURIComponent(tempIndexURL));

        tempState = encodeURIComponent(redirectURL),
        tempAuthorizeURL = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=' + appId + '&redirect_uri=' + tempRedirectURL + '&response_type=code&scope=snsapi_base&state=' + tempState + '#wechat_redirect';
    // alert(' tempAuthorizeURL:' + decodeURIComponent(tempAuthorizeURL));

    window.location.replace(tempAuthorizeURL);
};

//Rem
const initRem = function () {
    /*  */
    let docWidth = 750;

    let doc = window.document,
        docEl = doc.documentElement,
        resizeEvt = 'orientationchange' in window ? 'orientationchange' : 'resize';

    let recalc = function refreshRem() {
        let clientWidth = docEl.getBoundingClientRect().width;

        /* 8.55320px11.2420px */
        docEl.style.fontSize = Math.max(Math.min(20 * (clientWidth / docWidth), 11.2), 8.55) * 5 + 'px';

        return refreshRem;
    };

    if (!doc.addEventListener) {
        return
    };
    window.addEventListener(resizeEvt, recalc, false);
    doc.addEventListener('DOMContentLoaded', recalc, false);
};

//iOSopenId
const initIOSBackBtn = function () {

    let state = {
        title: "title",
        url: "#"
    };
    window.history.pushState(state, "title", "#");

    window.addEventListener("popstate", () => {
        if (window.wx) {
            window.wx.closeWindow();
        } else {
            window.close();
        }
    }, false);
};

let inT = {
    appId: null,
    openId: null,
    isUseWxSdk: false,
    isDebugMode: false,
    baseURL: null,
    publicPath: null,
    wxSignatureApi: null,
    wxAuthorizedApi: null,
    wxDebug: false,
    wxJsApiList: null,
    errorCodeValue: '00',
    commonErrorTips: '<br/>',
    isCheckErrorCode: false
};

class T {

    /**
     * 
     * 
     *
     */
    constructor(tConfig) {
        this.isNeedRedirect = false;//
        this.init(tConfig);
    }

    log(message) {
        if (inT.isDebugMode) {
            console.log('[vue-tcbj]' + message);
        };
    }


    /**
     * Get the version value.
     * @return {string} 
     *
     */
    get version() {
        return '1.0.14';
    }

    /**
     * Init the t.
     * 
     */
    init(tConfig) {


        inT = Object.assign({}, inT, tConfig);

        this.log('' + this.version);

        // initRem();
        this.getJsSdkSignature();


    }

    /**
     * Get the appId value.
     * @return {string} appId
     */
    getAppId() {
        return inT.appId;
    }

    /**
     * Get the openId value.
     * @return {string} openId
     */
    getOpenId() {
        return inT.openId;
    }

    /**
     * 
     * @return {string} baseURL
     */
    getBaseURL() {
        return inT.baseURL;
    }

    /**
     * 
     * @return {string} publicPath
     */
    getPublicPath() {
        return inT.publicPath;
    }
    /**
     * ajax
     * 
     * errorCodetConfigerrorCodeValue 
     * 
     * @param {Object} option - 
     * @param {string} opt.method -  get 
     * @param {string} opt.url - 
     * @param {Object} opt.params - 
     * @param {number} opt.timeout - 30000
     * @param {Object} opt.headers - 
     * @param {string} opt.responseType - text
     * @returns {object} axios instance - Promiseaxios
     * axioshttps://github.com/axios/axios
     */
    ajax(opt) {
        this.log('ajax');
        const instance = axios.create();

        const that = this;
        let tempMethod = opt.method || 'get',
            tempUrl = '',
            tempParams = opt.params || {},
            tempTimeout = 30000,
            tempIsStringifyPostData = false,
            tempHeaders = {},
            tempResponseType = 'text';

        // tempMethod = tempMethod.toUpperCase();

        if (opt.timeout !== undefined) {
            tempTimeout = opt.timeout;
        }

        if (typeof opt.headers === 'object') {
            tempHeaders = opt.headers;
        }

        if (opt.isDecodeURIComponent === false) {
            tempUrl = opt.url;
        } else {
            tempUrl = decodeURIComponent(opt.url);
        }

        if (tempMethod === 'get') {
            if (tempUrl.indexOf('?') === -1) {
                tempUrl += '?';
            }

            tempUrl += '&_random=' + new Date().getTime();
        }

        if ((tempMethod === 'put' || tempMethod === 'delete' || tempMethod === 'post' || tempMethod === 'patch') && tempIsStringifyPostData) {
            tempParams = JSON.stringify(tempParams);
        }

        //loader
        that.showLoader({
            duration: '10000000'
        });

        if (opt.responseType && (typeof opt.responseType === 'string') && opt.responseType.trim() !== '') {
            tempResponseType = opt.responseType;
        }

        //axios
        instance.interceptors.response.use((response) => {

            that.hideLoader();

            const data = response.data;

            if (data.errorCode !== inT.errorCodeValue && inT.isCheckErrorCode) {
                that.showAlert({
                    message: data.errorMessage + ' (' + data.errorCode + ')'
                });
            }

            return response.data;
        }, (error) => {
            that.showAlert({
                message: inT.commonErrorTips
            });
            this.log('');
            return Promise.reject(error);
        });

        let instanceObj = {
            method: tempMethod,
            url: tempUrl,
            timeout: tempTimeout,
            headers: tempHeaders,
            responseType: tempResponseType
        }

        if (tempMethod === 'get') {
            instanceObj.params = tempParams;
        } else if (tempMethod === 'post') {
            instanceObj.data = tempParams;
        }

        return instance(instanceObj);
    }

    /**
     *  JS 
     * tConfigJS
     * HTMLJSSDK
     * https://mp.weixin.qq.com/wiki?t=resource/res_main&id=mp1421141115
     */
    getJsSdkSignature() {
        this.log('getJsSdkSignature');

        let that = this;

        if (!inT.isUseWxSdk) {
            return;
        }

        // HTMLJSSDK
        if (!window.wx) {
            that.showAlert({
                message: 'jsSDK'
            });
            return;
        }

        let nonceStr = randomString(),
            timestamp = (new Date().getTime()).toString();

        //wxJsApiListapi
        const checkWxJSApiList = function (api) {
            return new Promise((resolve, reject) => {
                let hasApi = false;
                for (let i = 0; i < inT.wxJsApiList.length; i++) {
                    if (inT.wxJsApiList[i] === api) {
                        hasApi = true;
                        resolve();
                    }
                }

                if (hasApi === false) {
                    reject();
                }
            })
        };

        //closeWindowiOS
        checkWxJSApiList('closeWindow')
            .then(() => {
                that.log("tConfigcloseWindow");
            })
            .catch(() => {
                that.log("tConfigcloseWindow");
                inT.wxJsApiList.push('closeWindow');
            });

        const configWx = function (signature) {

            wx.config({
                debug: inT.wxDebug, // ,apialertpclogpc
                appId: inT.appId, // 
                timestamp: timestamp, // 
                nonceStr: nonceStr, // 
                signature: signature,// 
                jsApiList: inT.wxJsApiList // JS
            });

            wx.ready(function () {
                checkWxJSApiList('hideOptionMenu')
                    .then(() => {
                        that.log("");
                        wx.hideOptionMenu();
                    });
            });

            if (inT.wxDebug) {
                that.log('sdk')
            }
        };

        let opts = {
            url: inT.baseURL + inT.wxSignatureApi,
            method: 'get',
            params: {
                noncestr: nonceStr,
                timestamp: timestamp,
                url: decodeURIComponent(location.href.split('#')[0])
            }
        };

        //
        that.ajax(opts).then(data => {
            if (data.returnObject) {
                configWx(data.returnObject.signature);
            }
        });
    }

    /**
     * 
     * @param {string}
     * @returns {string}
     */
    getQueryString(name) {
        this.log('getQueryString name==' + name);

        const reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i');
        const r = window.location.search.substr(1).match(reg);

        if (r !== null) {
            return (r[2]);
        }

        return null;
    }

    /**
     * location hash
     * @param {string}
     * @returns {boolean}
     */
    checkLocationHashKeyExist(name) {
        this.log('checkLocationHashKeyExist name==' + name);

        let tempStr = decodeURIComponent(window.location.hash).substring(1),
            tempArray = tempStr.split('&'),
            tempArray2 = [],
            isKeyExist = false,
            i = 0,
            len = tempArray.length;

        for (; i < len; i++) {
            tempArray2 = tempArray[i].split('=');
            if (tempArray2[0] === name) {
                isKeyExist = true;
                break;
            }
        } // End for

        //this.log('isKeyExist=='+isKeyExist);
        return isKeyExist;
    }

    /**
     * location hash
     * @param {string} name
     * @param {string} value
     */
    updateLocationHashKey(name, value) {
        this.log('updateLocationHashKey name==' + name + ' value==' + value);

        if (this.checkLocationHashKeyExist(name)) {
            if (this.getLocationHashWithoutKey(name) === '') {
                window.location.hash = name + '=' + value;
            } else {
                window.location.hash = this.getLocationHashWithoutKey(name) + '&' + name + '=' + value;
            } // End if-else
        } else {
            this.addLocationHashKey(name, value);
        }
    }

    /**
     * location hash
     * @returns {boolean}
     */
    isLocationHashEmpty() {
        this.log('isLocationHashEmpty');

        let tempStr = window.location.hash;
        //this.log('isLocationHashEmpty tempStr=='+tempStr);
        return tempStr === ''
    }

    /**
     * location hash
     * @param {string} name
     * @returns {string}
     */
    getLocationHashWithoutKey(name) {
        this.log('getLocationHashWithoutKey name==' + name);

        let tempStr = decodeURIComponent(window.location.hash).substring(1),
            tempArray = tempStr.split('&'),
            tempArray2 = [],
            tempNewStr = '',
            i = 0,
            len = tempArray.length;

        for (; i < len; i++) {
            tempArray2 = tempArray[i].split('=');
            if (tempArray2[0] !== name) {
                tempNewStr += '&' + tempArray2[0] + '=' + tempArray2[1];
            }
        }

        return tempNewStr.substring(1);
    }

    /**
     * location hash
     * @param {string} name
     * @param {string} value
     */
    addLocationHashKey(name, value) {
        this.log('addLocationHashKey name==' + name + ' value==' + value);

        let tempStr = decodeURIComponent(window.location.hash);

        if (this.checkLocationHashKeyExist(name)) {
            this.updateLocationHashKey(name, value);
        } else {
            if (this.isLocationHashEmpty()) {
                tempStr = name + '=' + value;
            } else {
                tempStr += '&' + name + '=' + value;
            }

            window.location.hash = tempStr;
        }
    }

    /**
     * location hash
     * @param {string} name
     * @returns {string}
     */
    getLocationHashValue(name) {
        this.log('getLocationHashValue name===' + name);
        let tempStr = decodeURIComponent(window.location.hash).substring(1),
            tempArray = tempStr.split('&'),
            tempArray2 = [],
            returnValue = null,
            i = 0,
            len = tempArray.length;

        for (; i < len; i++) {
            tempArray2 = tempArray[i].split('=');
            if (tempArray2[0] === name) {
                returnValue = tempArray2[1];
                break;
            }
        }

        return returnValue;
    }

    /**
     * localStorage 
     * @param {string} name
     * @param {string} value
     */
    localStorage(name, value) {
        this.log('localStorage name==' + name + ' value==' + value);

        if (name === undefined || name === null || name.trim() === '') {
            return null;
        }

        if (value === undefined || value === null) {
            return localStorage.getItem(name);
        }

        if (typeof value === 'object') {
            value = JSON.stringify(value);
        }

        localStorage.setItem(name, value);
    }

    /**
     * 
     * https://wevue.org/doc/v2_0/dialog alert
     * @param {object} opt
     */
    showAlert(opt) {
        this.log('showAlert');

        if (!opt.title) {
            opt.title = '';
        }
        Dialog.alert(opt);
    }

    /**
     * 
     * https://wevue.org/doc/v2_0/dialog confirm
     * @param {object} opt
     */
    showConfirm(opt) {
        this.log('showConfirm');
        return Dialog.confirm(opt)
    }

    /**
     * LoaderhideLoader()
     * https://wevue.org/doc/v2_0/toast loading
     * @param {object}
     */
    showLoader(tempOpt) {
        this.log('showLoader');

        if (tempOpt === undefined || tempOpt === null || tempOpt === '') {
            tempOpt = {
                message: ''
            }
        }

        Toast.loading(tempOpt);
    }

    /**
     * Loader
     */
    hideLoader() {
        this.log('hideLoader');
        Toast.close();
    }

    /**
     * openId
     * 
     * tConfigopenId
     * 
     * @param {string} params publicPathkeyName,keyValue .
     * 
     * tConfig wxAuthorizedApi  appId
     */
    getOpenIdFromWx(params) {
        this.log('getOpenIdFromWx');

        // alert('111 url===' + location.href)

        if (inT.openId) {
            return;
        }

        let tempPublicPath;

        if (params) {
            tempPublicPath = inT.publicPath + ',' + params;
        } else {
            tempPublicPath = inT.publicPath
        }

        inT.openId = this.getQueryString('openId');
        // alert('111 openId===' + this.openId);
        if (this.cookie('openId') && !inT.openId) {
            inT.openId = this.cookie('openId');
            // alert('222 openId===' + this.openId);
        }

        if (!inT.openId) {
            // alert('isNeedRedirect');
            this.isNeedRedirect = true;
            // alert('weChatAuthRedirect');
            weChatAuthRedirect(inT.appId, this.getBaseURL() + inT.wxAuthorizedApi, tempPublicPath);
            // alert('redirectEnd');
        }

        if (this.isNeedRedirect) {
            return;
        }

        initIOSBackBtn();
    }

    /**
     * cookie 
     * @param {string} name
     * @param {string} value
     * @param {object} options
     * @returns {string}
     */
    cookie(name, value, options) {
        this.log('cookie name==' + name + ' value==' + value + ' options==' + options);

        if (typeof value !== 'undefined') {
            options = options || {};
            if (value === null) {
                value = '';
                options.expires = -1;
            }
            let expires = '';
            if (options.expires && (typeof options.expires === 'number' || options.expires.toUTCString)) {
                let date;
                if (typeof options.expires === 'number') {
                    date = new Date();
                    date.setTime(date.getTime() + (options.expires * 24 * 60 * 60 * 1000));
                } else {
                    date = options.expires;
                }
                expires = ';expires=' + date.toUTCString();
            }
            let path = options.path ? ';path=' + options.path : ';path=/';
            let domain = options.domain ? ';domain=' + options.domain : '';
            let secure = options.secure ? ';secure' : '';
            document.cookie = [name, '=', encodeURIComponent(value), expires, path, domain, secure].join('');
        } else {
            let cookieValue = '';
            if (document.cookie && document.cookie !== '') {
                let cookies = document.cookie.split(';');
                for (let i = 0; i < cookies.length; i++) {
                    let cookie = cookies[i].trim();
                    if (cookie.substring(0, name.length + 1) === (name + '=')) {
                        cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
                        break;
                    }
                }
            }
            return cookieValue;
        }
    }

}

export default {
    install(Vue, tConfig) {
        Vue.prototype.t = new T(tConfig);
    }
};


