<template>
    <div id="app">
        <wv-button type="primary" @click="getOpenId">openId</wv-button>
        <wv-button type="primary" @click="getVersion"></wv-button>
        <wv-button type="primary" @click="showLoader">loader</wv-button>
        <wv-button type="primary" @click="showAlert"></wv-button>
        <wv-button type="primary" @click="showConfirm"></wv-button>
        <wv-button type="primary" @click="getUserInfo">getUserInfo</wv-button>
        <wv-button type="primary" @click="getEasCode">getEasCode</wv-button>
        <wv-button type="primary" @click="getLocationHashValue">Hash</wv-button>
        <wv-button type="primary" @click="updateLocationHashKey">Hashname</wv-button>
        <wv-button type="primary" @click="getQueryString"></wv-button>
        <wv-button type="primary" @click="setLocalStorage">localStoragenametcbj</wv-button>
        <wv-button type="primary" @click="getLocalStorage">localStoragename</wv-button>
        <wv-button type="primary" @click="setCookie">cookiemyName</wv-button>
        <wv-button type="primary" @click="getCookie">cookiemyName</wv-button>
        <wv-button type="primary" @click="qrCode"></wv-button>
    </div>
</template>

<script>
export default {
  name: "app",
  data() {
    return {};
  },
  mounted() {
    this.t.getOpenIdFromWx("securityCode,123456");

    this.t.showAlert({
      message: this.t.getQueryString("securityCode")
    });
  },
  methods: {
    getOpenId() {
      this.t.showAlert({
        message: this.t.getOpenId()
      });
    },

    getVersion() {
      this.t.showAlert({
        message: this.t.version
      });
    },
    showLoader() {
      this.t.showLoader();
    },

    showAlert() {
      this.t.showAlert({
        message: ""
      });
    },

    showConfirm() {
      this.t
        .showConfirm({
          title: "",
          message: " we-vue!",
          showCancelButton: true
        })
        .then(() => {
          console.log("confirmed");
        })
        .catch(() => {
          console.log("canceled");
        });
    },

    getUserInfo() {
      console.log("fetchData");

      this.api.getUserInfo();
    },

    getEasCode() {
      this.api.getEasCode();
    },

    getLocationHashValue() {
      this.t.showAlert({
        message: this.t.getLocationHashValue("name")
      });
    },

    getQueryString() {
      this.t.showAlert({
        message: this.t.getQueryString("name")
      });
    },

    setLocalStorage() {
      this.t.localStorage("name", "tcbj");
    },

    getLocalStorage() {
      this.t.showAlert({
        message: this.t.localStorage("name")
      });
    },

    updateLocationHashKey() {
      let value = "baiyue";
      this.t.updateLocationHashKey("name", value);
      console.log("locationHashKey ===" + value);
    },

    setCookie() {
      this.t.cookie("myName", "huohuohuo");
    },

    getCookie() {
      this.t.showAlert({
        message: this.t.cookie("myName")
      });
    },

    qrCode() {
      wx.ready(function() {
        // configreadyconfigconfigreadyready
        wx.scanQRCode({
          needResult: 0, // 01
          scanType: ["qrCode"], // 
          success: function(res) {},
          complete: function() {
            wx.closeWindow();
          }
        });
      });
    }
  }
};
</script>

<style>
body {
  margin: 0;
  padding: 20px 10px;
}
</style>


